<template>
  <v-card class="pa-4">
    <h2>Outstanding Master List</h2>
    <v-row dense>
      <v-col cols="12" md="4">
        <v-menu v-model="menuDate" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedDate"
              label="As Of Date"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedDate"
            type="date"
            @change="menuDate = false"
          />
        </v-menu>
      </v-col>
      <v-col cols="12" md="4" class="d-flex align-end">
        <v-btn
          color="success"
          @click="exportExcel"
          :disabled="!selectedDate"
          :loading="exporting"
        >
          <v-icon left>mdi-file-excel</v-icon>
          Export Outstanding Master List
        </v-btn>
      </v-col>
    </v-row>
    <v-alert type="info" outlined class="mt-4">
      The exported Excel file will contain the outstanding master list as of
      your selected date.
    </v-alert>
  </v-card>
</template>
<script>
import moment from "moment";
import { apiConfig } from "../../config/api.config";

export default {
  name: "OutstandingMaster",
  data() {
    return {
      selectedDate: null,
      menuDate: false,
      exporting: false,
    };
  },
  methods: {
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          date: moment(this.selectedDate).format("YYYY-MM-DD"),
        };
        const url =
          `${apiConfig.baseURL}/compliance/outstanding-master/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
  },
};
</script>
